import React, { Component } from 'react'
import { DELETE_RADIS_KEYS } from '../../../actions/themeActions';
import { NoCameraImg } from '../../../constants';
import { GET_MEDIA_NAME, TIKTOK_UPDATE_STATUS } from '../../../utils';

export default class tikTok_Img_update extends Component {
    imageQueue = [];
    state = { firstRequest: false }


    handleImageError = (e) => {
        const src = e.target.getAttribute("data-id");
        if (!this.imageQueue.includes(src)) {
            this.imageQueue.push(src);
            if (!this.state.firstRequest) {
                this.processImageQueue()
            }
        }
    };

    processImageQueue = async () => {
        this.setState({ firstRequest: true })
        const src = this.imageQueue[0];
        const imgTargets = document.getElementsByClassName(`imgID${src}`);
        if (src) {
            TIKTOK_UPDATE_STATUS(src).then(({ data }) => {
                DELETE_RADIS_KEYS();
                this.imageQueue.shift();
                if (imgTargets) {
                    for (let i = 0; i < imgTargets.length; i++) {
                        imgTargets[i].src = data.media;
                    }
                }
                this.processImageQueue()
            })
                .catch((error) => {
                    this.imageQueue.shift();
                    if (imgTargets) {
                        for (let i = 0; i < imgTargets.length; i++) {
                            imgTargets[i].src = NoCameraImg;
                        }
                    }
                    this.processImageQueue()
                })

        }
    };


    render() {
        const { postData, completeDataObject } = this.props;

        return (postData ? postData.map((item, index) => {
            const cardData = completeDataObject[item];
            return cardData && cardData.network.id == 28 ? <img key={index} className="" data-id={cardData.id} src={cardData.postFileNew}
                onError={this.handleImageError} style={{ display: 'none' }} alt={GET_MEDIA_NAME(cardData.postFileNew)} /> : null
        }) : null)
    }
}